html {
  color: aquamarine;
  background-color: black;
}

body,
h2,
p {
  margin: 0;
}

.Todo {
  &__welcome {
    margin: 30px 0;
    display: flex;
    justify-content: center;
  }

  &__form {
    margin: 0 auto 50px;
    display: grid;
    justify-items: center;
    width: max-content;
    gap: 10px;
  }

  &__list {
    list-style: none;
    padding: 0;
    width: 1000px;
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__item {
    background-color: aquamarine;
    border-radius: 10px;
    color: black;
    padding: 15px;

    align-items: center;
    justify-content: center;
  }

  &__textarea {
    font-family: inherit;
    font-size: 18px;

    width: 400px;
    height: 100px;

    color: inherit;
    background: black;

    border-radius: 10px;
    padding: 5px;
    border: 1px solid aquamarine;
    outline: none;

    resize: none;

    &::placeholder {
      color: inherit;
    }
  }

  &__select {
    font-size: 18px;

    background: black;
    color: inherit;

    height: 2em;
    width: 50%;

    border-radius: 10px;
    padding: 5px;
    border: 1px solid aquamarine;
    outline: none;
  }

  &__button {
    display: inline-block;
    position: relative;
    text-align: center;
    text-decoration: none;

    width: auto;
    outline: none;
    padding: 13px;
    margin: 0;
    cursor: pointer;

    font-size: 16px;
    font-weight: 600;
    line-height: 10px;

    background-color: aquamarine;
    border: 1px solid #222;
    border-radius: 10px;
    box-sizing: border-box;
    color: #222;

    touch-action: manipulation;
    user-select: none;
    -webkit-user-select: none;
  }

  &__error {
    font-size: 15px;
  }

  &__info {
    font-weight: 700;

    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
  }
}



